import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/common/Home.vue'
import Login from '../components/blocks/Login.vue'
import Welcome from '../components/blocks/Welcome.vue'
import Order from '../components/blocks/Order.vue'
import Foods from '../components/blocks/Foods.vue'
import Cover from '../components/blocks/Cover.vue'
import Member from '../components/blocks/Member.vue'
import Business from '../components/blocks/Business.vue'
import AddFoods from '../components/blocks/AddFoods'
import EditFoods from '../components/blocks/EditFoods'
import ViewFoods from '../components/blocks/ViewFoods'
import Category from '../components/blocks/Category'


Vue.use(VueRouter)

const routes = [{
        path: '/',
        redirect: '/login'
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
        redirect: '/welcome',
        meta: ['首页'],
        children: [{
                path: '/welcome',
                component: Welcome,
                meta: ['首页']
            },
            {
                path: '/order',
                name: 'Order',
                component: Order,
                meta: ['订单管理', '订单列表'],
            },
            {
                path: '/foods',
                name: 'Foods',
                component: Foods,
                meta: ['商品管理', '商品列表'],
            },
            {
                path: '/addfoods',
                name: 'AddFoods',
                component: AddFoods,
                meta: ['商品管理', '添加商品'],
            },
            {
                path: '/viewfoods',
                name: 'ViewFoods',
                component: ViewFoods,
                meta: ['商品管理', '商品列表', '查看商品'],
            },
            {
                path: '/category',
                name: 'Category',
                component: Category,
                meta: ['商品管理', '分类列表'],
            },
            {
                path: '/editfoods',
                name: 'EditFoods',
                component: EditFoods,
                meta: ['商品管理', '商品列表', '编辑商品'],
            },
            {
                path: '/cover',
                component: Cover,
                meta: ['商品管理', '设置封面'],
            },
            {
                path: '/member',
                component: Member,
                meta: ['会员管理', '会员列表'],
            },
            {
                path: '/business',
                component: Business,
                meta: ['商务合作', '发布推文'],
            }
        ]
    }, {
        path: '/login',
        name: 'Login',
        component: Login
    }
]

const router = new VueRouter({
    routes,
    // mode: 'history',
    // base: process.env.BASE_URL,
})

// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
    // to将要访问的路径
    // from从哪个路径跳转而来
    // next 是一个函数 表示放行
    // next() 放行 next('/login) 强制跳转
    if (to.path === '/login') return next();
    // 获取token
    const tokenStr = window.localStorage.getItem('admin_token');
    if (!tokenStr) return next('/login');
    next();
})

export default router